.main {
    display: flex;
    align-items: center;
    flex-direction: column;
    justify-content: space-around;
    margin-top: 1.5rem;
}

.me { 
    border-radius: 0.5rem;
    width: -webkit-fill-available;
    border: 0.05rem solid #49111C;
    box-shadow: 0rem 0rem 0.7rem 0rem #49111C;
}

.list {
    border: 0.05rem solid #49111C;
    border-radius: 0.5rem;
    padding: 0.05rem;
    box-shadow: 0rem 0rem 0.7rem 0rem #49111C;
}

.list p{
    margin: 0rem 0rem 0rem 0.7rem;
}

.text {
    margin-top: 40px;
    font-size: 4vw
}

.picture-section {
    width: 80vw;
}

.about-me {
    border: 0.05rem solid ;
    box-shadow: 0rem 0rem 0.7rem 0rem #49111C;
    border-radius: 0.5rem;
    padding: 0.5rem;
    margin: 0;
    text-align: center;
}

@media screen and (min-width: 700px) {
    .main {
        width: 100%;
        flex-direction: row;
    }

    .picture-section {
        width: 30vw;
    }

    .text {
        width: 40%;
    }

    .about-me {
        text-align: start;
        font-size: 2vw;
    }
}